<template>
  <el-dialog
    class="dialog-d dialog-body-p-0 dialog-footer-p-0"
    :modelValue="modelValue"
    width="90%"
    top="20vh"
    :destroy-on-close="true"
    :close-on-click-modal="false"
    :show-close="false"
    append-to-body
    @close="onClose"
  >
    <template #header>
      <Row class="font-38 bold" x="s" y="s">
        {{ $t('ieo.subscription') }}
      </Row>
    </template>
    <el-form ref="formRef" :model="form" class="form p-lr-40 m-t-10 f-column">
      <el-form-item class="f-column m-b-5" prop="copies">
        <template #label>
          <Row class="w-100b m-b-4 label-sm" x="s" y="s">
            <span>{{ $t('ieo.copiesNum') }}</span>
          </Row>
        </template>
        <!-- {{ Number(ieoSubscriptionNum) }} - {{ form.copies }} -->
        <Row class="w-100b">
          <el-input
            :modelValue="form.copies"
            class="f-1"
            type="number"
            :placeholder="$t('form.inputPl','ieo.copiesNum')"
            oninput="value=value.replace(/[^0-9]/g,'')"
            @input="onChange"
            @keyup.enter="submit"
          >
            <template #suffix>
              <el-button link type="primary" @click="toMax"><span class="label bold">{{ $t('form.max') }}</span></el-button>
            </template>
          </el-input>
        </Row>
        <Column class="w-100b m-t-30">
          <Row class="w-100b label-sm m-b-15 lh-no" x="sb">
            <span class="c-info m-r-5">{{ $t('public.pay') }}</span>
            <span class="bold">{{ !isEmpty(form.copies) ? (form.copies * preAmountMoney).toFixed(5) : '' }}</span>
          </Row>
          <Row class="w-100b label-sm m-b-15 lh-no" x="sb">
            <span class="c-info m-r-5">{{ $t('form.accountAvailable') }}</span>
            <span class="bold">{{ buyAvailable }}</span>
          </Row>
          <Row class="w-100b label-sm m-b-15 lh-no" x="sb">
            <span class="c-info m-r-5">{{ $t('ieo.numberSubscriptionCoupons') }}</span>
            <span class="bold">{{ ieoSubscriptionNum }}</span>
          </Row>
          <Row class="w-100b label-sm m-b-15 lh-no" x="sb">
            <span class="c-info m-r-5">{{ $t('ieo.pricePerServing') }}</span>
            <span class="bold">{{ preAmountMoney }}</span>
          </Row>
        </Column>
        <template #error>
          <Row class="w-100b c-error label-sm lh-no m-t-5">{{ $t('form.inputPl','form.subscriptionAmount') }}</Row>
        </template>
      </el-form-item>
    </el-form>
    <Row class="p-lr-40 w-100b m-b-40">
      <Row class="w-100b p-lr-20 p-tb-25 bg-d c-info label-sm radius-10" x="s">
        {{ $t('ieo.dialogSubscribeHint') }}
      </Row>
    </Row>
    <template #footer>
      <Row class="p-lr-40 p-b-35" x="e" y="c">
        <el-button
          class="p-lr-25 f-1 m-r-20"
          @click="onClose"
        ><span class="label-lg bold">{{ $t('form.cancel') }}</span></el-button>
        <el-button
          class="p-lr-25 f-1"
          type="primary"
          :loading="loading"
          :disabled="submitDisabled"
          @click="submit"
        ><span class="label-lg bold">{{ $t('form.confirmInput') }}</span></el-button>
      </Row>
    </template>

  </el-dialog>
</template>
<script setup>
import { isEmpty } from '../../../../utils/globalFunc'

const emit = defineEmits(['submit'])
const props = defineProps({
  modelValue: {},
  id: {},
  preAmountMoney: {},
  ieoSubscriptionNum: {}
})
const userStore = useUserStore()

const form = reactive({
  copies: 0
})
const buyAvailable = $computed(() => userStore.accountInfo['1']?.coinAccountList.find(e => e.coinName == 'USDT').amount)

let loading = $ref(false)

const formRef = $ref(null)

const submitDisabled = $computed(() => {
  return !form.copies
})

const toMax = () => {
  form.copies = props.ieoSubscriptionNum
}

const onChange = (v) => {
  let value = v
  if (/^0\d$/.test(value)) {
    value = value.toString().slice(1)
  }
  if (value > props.ieoSubscriptionNum) {
    form.copies = props.ieoSubscriptionNum
  } else {
    form.copies = value
  }
}

const submit = async () => {
  try {
    loading = true
    const data = await ieoApi.subscribe({
      ieoId: props.id,
      copies: form.copies,
    })
    await userStore.getAccountInfo()
    showNotify({ type: 'success', message: $t('ieo.subscription', 'alert.success') })
    emit('update:modelValue', false)
    emit('submit')
  } finally {
    loading = false
  }
}

const onClose = () => {
  emit('update:modelValue', false)
}

watch(() => props.modelValue, (show) => {
  if (show == false) {
    form.copies = 0
  }
})

onBeforeMount(() => {
})
</script>

