<template>
  <Page
    :title="$t('ieo.projectDetails')"
    :leftArrow="true"
    hideHeader
    bodyClass="w-100b bg-white ProjectInfo"
    :onClickRight="toSubscriptionHistory"
    :onClickLeft="onClickLeft"
  >
    <template #left>
      <van-icon class="title c-info" name="arrow-left" />
    </template>
    <template #right>
      <img class="w-32 h-32" src="@/image/ieo/toHistory.webp">
    </template>
    <ProjectInfoHeader
      :id="id"
      class="w-100b m-b-60"
      :loading="loading"
      :status="data.projectProcess"
      :name="data.name"
      :fullName="data.fullName"
      :synopsis="getSynopsis(data)"
      :model="data.ieoModel"
      :imgUrl="data.imgUrl"
      :totalCopies="data.totalCopies ?? 0"
      :preAmount="data.preAmount"
      :preAmountMoney="data.preAmountMoney"
      :currentNum="data.currentNum"
      :currentCopiesNum="data.currentCopiesNum ?? 0"
      :twitterLink="data.twitterLink"
      :telegramLink="data.telegramLink"
      :projectWhitePaper="data.projectWhitePaper"
      :projectWebsite="data.projectWebsite"
      :subscriptionNum="data.subscriptionNum"
      :ieoCoupons="userData.ieoCoupons"
      :subscriptionStatus="userData.subscriptionStatus"
      :ieoSubscriptionNum="userData.ieoSubscriptionNum"
      :isLock="data.isLock"
      :startTime="data.startTime"
      :endTime="data.endTime"
      :subStartTime="data.subStartTime"
      :subEndTime="data.subEndTime"
      :inputStatus="data.inputStatus"
      :subStatus="subStatus"
      :countdown="countdown"
      @refresh="getData"
    />
    <Column v-if="Object.keys(tasks)?.length > 0" class="w-100b p-lr-30 bg-white">
      <Column class="w-100b m-b-30">
        <Row class="w-100b f-wrap" x="sb">
          <ProjectTask
            v-for="task in tasksSort"
            :key="task.taskType"
            :data="task"
            :status="data.projectProcess"
            :taskStatus="task.taskStatus"
            :type="task.taskType"
            :name="data.name"
            :symbolsAll="symbolsAll"
            class="w-100b m-b-30"
          />
        </Row>
        <Row
          v-if="tasksLen > taskShowNum"
          class="w-100b"
          x="c"
          y="c"
          @click="() => taskOpen = !taskOpen"
        >
          <span v-if="taskOpen" class="c-info label">{{ $t('ieo.close') }}</span>
          <span v-else class="c-info label">{{ $t('ieo.open') }}</span>
          <img :class="taskOpen ? '' : 'rotateZ-180'" class="w-23 h-14 m-l-10" src="@/image/ieo/top-arrow.webp">
        </Row>
      </Column>
    </Column>
    <Column v-show="!loading" v-if="data.isLock == 1" class="w-100b p-lr-30 m-b-70">
      <Row class="m-b-30" x="s" y="c">
        <div class="h-32 w-8 radius-4 bg-primary m-r-8" />
        <span class="title-sm bold">{{ $t('ieo.tokenDistribution') }}</span>
      </Row>
      <Column class="w-100b" x="s" y="c">
        <span class="label lh-44 m-b-10">{{ $t.replace('ieo.holdMinimum200BFT', {num:data.activityHoldAmount}) }}</span>
        <Column class="w-100b p-t-70 p-b-60 m-b-40 position-r border border-c-placeholder radius-8" x="c">
          <img src="@/image/ieo/tokenDistribution-1.webp" class="w-156 h-156 position-a-tl" alt="">
          <Row class="w-156 h-156 position-a -top-14 -left-14 label-sm" x="c" y="c" :style="{'transform': 'rotate(-45deg)'}">{{ $t('ieo.defaultUnlockCycle') }}</Row>
          <span class="label-sm c-info">{{ $t('ieo.initialUnlockRatio') }}</span>
          <span class="font-40 m-t-6 m-b-30">{{ BigNumber(data.firstProportion ?? 0).times(100).toNumber() }}%</span>
          <Row class="w-100b p-lr-30">
            <Column class="w-100b border-t border-l">
              <Row class="w-100b bg-line label-sm c-info">
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.remainingUnlockPeriod') }}</Row>
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.daysFirstDistribution') }}</Row>
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.ratio') }}</Row>
              </Row>
              <Row v-for="(row) in data.userJson" :key="row.cycle" class="w-100b label-sm">
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ row.cycle }}</span>
                </Row>
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ row.days }}</span>
                </Row>
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ BigNumber(row.proportion ?? 0).times(100).toNumber() }}</span>%
                </Row>
              </Row>
            </Column>
          </Row>
        </Column>
        <Column class="w-100b p-t-70 p-b-60 position-r border border-c-placeholder radius-8" x="c">
          <img src="@/image/ieo/tokenDistribution-2.webp" class="w-156 h-156 position-a-tl" alt="">
          <Row class="w-156 h-156 position-a -top-14 -left-14 c-white label-sm" x="c" y="c" :style="{'transform': 'rotate(-45deg)'}">{{ $t('ieo.acceleratedUnlockCycle') }}</Row>
          <span class="label-sm c-info">{{ $t('ieo.initialUnlockRatio') }}</span>
          <span class="font-40 m-t-6 m-b-30">{{ BigNumber(data.firstProportionLock ?? 0).times(100).toNumber() }}%</span>
          <Row class="w-100b p-lr-30">
            <Column class="w-100b border-t border-l">
              <Row class="w-100b bg-line label-sm c-info">
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.remainingUnlockPeriod') }}</Row>
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.daysFirstDistribution') }}</Row>
                <Row class="f-1 col h-68" x="c" y="c">{{ $t('ieo.ratio') }}</Row>
              </Row>
              <Row v-for="(row) in data.userLockJson" :key="row.cycle" class="w-100b label-sm">
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ row.cycle }}</span>
                </Row>
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ row.days }}</span>
                </Row>
                <Row class=" f-1 col" x="c" y="c">
                  <span>{{ BigNumber(row.proportion ?? 0).times(100).toNumber() }}</span>%
                </Row>
              </Row>
            </Column>
          </Row>
          <Row class="m-t-30 w-100b p-lr-30" x="sb">
            <Column class="label-sm lh-34">
              <span>{{ $t('ieo.myBFT') }}</span>
              <span>{{ userData.holdAmountMoney ?? 0 }}</span>
            </Column>
            <el-button
              v-if="userData.type == 0"
              :disabled="(now>data.tokenIssuanceTime || ![2,3].includes(data.projectProcess))"
              class="w-min-322 h-70 label"
              type="primary"
              @click="showDialogTokenDistribution"
            >{{ $t('ieo.activateAcceleration') }}</el-button>
            <Row v-if="userData.type == 1" x="c" y="c" class="w-322 h-70 label not-allowed bg-placeholder c-white radius-4">{{ $t('ieo.accelerated') }}</Row>
          </Row>
        </Column>
        <Row class="label m-t-30">
          <span>{{ $t('ieo.APR') }}:</span>
          <span v-for="(item,index) in data.incomeCoin" :key="item.coinName">{{ index>0 ? ',' : '' }}{{ item.coinName }}/{{ BigNumber(item.rate ?? 0).times(100).toNumber() }}%</span>
        </Row>
      </Column>
    </Column>
    <Column v-show="!loading" class="w-100b p-lr-30 m-b-70">
      <Row class="m-b-22" x="s" y="c">
        <div class="h-32 w-8 radius-4 bg-primary m-r-8" />
        <span class="title-sm bold">{{ $t('public.ruleOfActivity') }}</span>
      </Row>
      <Column class="w-100b p-30 bg-d radius-4" x="s" y="c">
        <div class="" v-html="getRules(data)" />
      </Column>
    </Column>
    <DialogTokenDistribution
      v-model="dialogTokenDistributionData.show"
      :type="dialogTokenDistributionData.type"
      :ieoId="id"
      @submit="refresh"
    />
  </Page>
</template>
<script setup>
import { DialogTokenDistribution, ProjectInfoHeader, ProjectTask, } from '@/view/ieo/components/index'

const props = defineProps({
  id: {},
})
const languageStore = useLanguageStore()
const route = useRoute()
const userStore = useUserStore()
const showDialogSubscribe = $ref(false)
let loading = $ref(true)

const data = reactive({
  totalCopies: 0,
  currentCopiesNum: 0,
})
const userData = reactive({})
const tasks = reactive({})
const taskShowNum = 3
const taskOpen = $ref(false)
const dialogTokenDistributionData = reactive({
  show: false,
  type: null,
})
const now = Date.now()

const getSynopsis = (item) => {
  const d = item?.languageJson?.find(e => e.language == languageStore.locale) ?? {}
  return d.introduction
}

const getRules = (item) => {
  const d = item?.languageJson?.find(e => e.language == languageStore.locale) ?? {}
  return d.rules
}

const showDialogTokenDistribution = () => {
  dialogTokenDistributionData.show = true
  dialogTokenDistributionData.type = userData.holdAmountMoney >= data.activityHoldAmount ? 1 : 2
}

let subStatus = $ref(null)
const refreshSubStatus = () => {
  const now = dayjs()
  let val
  if (now.isBefore(data.startTime)) {
    val = 1
  } else if (now.isBefore(data.subStartTime)) {
    val = 2
  } else if (now.isBefore(data.subEndTime)) {
    val = 3
  } else if (now.isBefore(data.endTime)) {
    val = 4
  } else {
    val = 5
  }
  subStatus = val
}

const time = $computed(() => {
  const map = {
    1: data.startTime,
    2: data.subStartTime,
    3: data.subEndTime,
    4: data.endTime,
  }
  return map[subStatus]
})

const { countdown } = useCountdown(() => time)

const getData = async () => {
  // await delay(50000)
  const d = await ieoApi.info({ id: props.id })
  const userD = await ieoApi.userIeoInfo({ ieoId: props.id, })

  d.startTime = Number(d.startTime)
  d.subStartTime = Number(d.ieoSubscribeStartTime)
  d.subEndTime = Number(d.ieoSubscribeEndTime)
  d.endTime = Number(d.tokenIssuanceTime)
  const taskStatusKey = {
    0: 'tradeTask',
    1: 'depositTask',
    2: 'holdTask',
    3: 'invitedTask',
    4: 'kycTask',
    5: 'rateLevelTask',
  }
  for (const k in d) {
    data[k] = d[k]
  }
  for (const k in userD) {
    userData[k] = userD[k]
  }

  d.ieoSubtasksVOS.forEach((e, i) => {
    tasks[e.taskType] = {
      holdDays: e.holdDays,
      taskCondition: e.taskCondition,
      taskNum: e.taskNum,
      coinName: e.coinName,
      taskTime: e.taskTime,
      taskType: e.taskType,
      isAnyCoin: e.isAnyCoin,
      isContinue: e.isContinue,
      taskStatus: userD[taskStatusKey[e.taskType]],
      sort: i,
    }
  })
}

const tasksLen = $computed(() => Object.keys(tasks).length)
const tasksSort = $computed(() => {
  const sorts = Object.keys(tasks).map(k => tasks[k].sort).sort((a, b) => a - b)
  const taskList = Object.keys(tasks).map(k => tasks[k])
  const list = []
  for (const i in sorts) {
    const s = sorts[i]
    list.push(taskList.find(e => e.sort == s))
  }
  if (taskOpen) {
    return list
  } else {
    return list.slice(0, taskShowNum)
  }
})

const refresh = async () => {
  try {
    loading = true
    await getData()
  } finally {
    loading = false
  }
}

const timelineTimes = $computed(() => [
  dayjs(data.startTime).format('YYYY-MM-DD HH:mm'),
  dayjs(data.subStartTime).format('YYYY-MM-DD HH:mm'),
  dayjs(data.subEndTime).format('YYYY-MM-DD HH:mm'),
  dayjs(data.endTime).format('YYYY-MM-DD HH:mm'),
])

const timelineLabels = $computed(() => [$t('ieo.activityStarts'), data.type == 2 ? $t('ieo.airdropBegins') : $t('ieo.subscriptionStarts'), $t('ieo.tokenAllocation'), $t('ieo.assignmentEnded')])

const toSubscriptionHistory = () => {
  const query = reserveUrlQuery(route)

  router.push({
    path: '/ieo/subscriptionHistory',
    query: query,
  })
}

let symbolsAll = $ref([])
const getSymbolsAll = async () => {
  const data = await homeApi.coinMarketsList()
  symbolsAll = data
}

const onClickLeft = () => {
  router.go(-1)
}

useAddTimer(refreshSubStatus)

watch(() => subStatus, () => {
  getData()
})

winMes.postParent('hiddenNaviBar')

onBeforeMount(() => {
  refresh()
  getSymbolsAll()
  inModule(['/ieo', '/ieo/subscriptionHistory'])
})

</script>

<style lang="scss">
@import '@/sass/var/index.scss';

.ProjectInfo{
  .col{
    height: 80px;
    border-bottom: 1px solid $color-placeholder;
    border-right: 1px solid $color-placeholder;
  }
}
</style>
