                <van-skeleton-image class="skeletonImage" image-shape="round" />
              </Row>
            </Column>
            <!-- <Row x="sb">
              <van-skeleton-paragraph round row-width="30%" />
              <van-skeleton-paragraph round row-width="30%" />
            </Row> -->
          </template>
        </van-skeleton>
      </Column>
    </Column>
    <Column v-show="!loading" class="w-100b p-lr-30">
      <Column class="w-100b -m-t-30 bg-white p-30 radius-8 z-10 m-b-40" y="sb" :style="{'box-shadow': '0px 0px 20px 1px rgba(38,73,239,0.08)'}">
        <Column class="w-100b">
          <Row class="m-b-24">
            <span class="title-sm bold m-r-6">{{ name }}</span>
            <span class="label-sm c-info m-r-6">{{ fullName }}</span>
            <span class="c-primary label-sm bg-primary-10 p-lr-10 p-tb-2 radius-18">{{ modelLabel }}</span>
          </Row>
          <div class="label c-info m-b-20 lh-40 text-left ellipsis-3">{{ synopsis }}</div>
        </Column>
        <Column class="w-100b">
          <Row class="w-100b m-b-16 label" x="sb">
            <span class="c-info m-r-10">{{ $t('ieo.numberCopiesSale') }}</span>
            <span class="bold">
              <span>{{ totalCopies }}</span>
              <span class="m-l-5">{{ $t('public.share') }}</span>
            </span>
          </Row>
          <Row class="w-100b m-b-16 label" x="sb">
            <span class="c-info m-r-10">{{ $t('ieo.quantityPerServing') }}</span>
            <span class="bold">
              <span>{{ preAmount }}</span>
              <span class="m-l-5">{{ name }}</span>
            </span>
          </Row>
          <Row class="w-100b m-b-16 label" x="sb">
            <span class="c-info m-r-10">{{ $t('ieo.pricePerServing') }}</span>
            <span class="bold">
              <span>{{ preAmountMoney }}</span>
              <span class="m-l-5">USDT</span>
            </span>
          </Row>
          <Row class="w-100b m-b-16 label" x="sb">
            <span class="c-info m-r-10">{{ $t('ieo.subscribers') }}</span>
            <span class="bold">
              <span>{{ isEmpty(currentNum) ? $t('form.notStartedYet') : currentNum }}</span>
            </span>
          </Row>
          <Row class="w-100b m-b-26 label" x="sb">
            <span class="c-info m-r-10">{{ $t('ieo.currentCopiesNum') }}</span>
            <span class="bold">
              <span>{{ isEmpty(currentCopiesNum) ? $t('form.notStartedYet') : currentCopiesNum }}</span>
              <span class="c-info m-lr-4">/</span>
              <span class="c-info">{{ totalCopies }}</span>
            </span>
          </Row>
          <el-progress v-if="!isEmpty(currentCopiesNum) && !isEmpty(totalCopies)" :percentage="+($f.toFixed(((currentCopiesNum??0) / (totalCopies??0)) * 100, 2))" class="w-100b h-8 m-b-26 ">
            <template #default="{ percentage }">
              <Row class="bold" x="e">
                {{ percentage??0 }}%
              </Row>
            </template>
          </el-progress>
          <Row class="">
            <img v-if="telegramLink" src="@/image/ieo/telegram.webp" class="w-56 h-56 m-r-20" @click="open(telegramLink)">
            <img v-if="twitterLink" src="@/image/ieo/twitter.webp" class="w-56 h-56 m-r-20" @click="open(twitterLink)">

            <el-button
              v-if="projectWhitePaper"
              class="label-sm h-auto m-r-20"
              type="info"
              link
              @click="open(projectWhitePaper)"
            ><span class="label-sm">{{ $t("public.whitePaper") }}</span> </el-button>
            <el-button
              v-if="projectWebsite"
              class=" h-auto"
              type="info"
              link
              @click="open(projectWebsite)"
            ><span class="label-sm">{{ $t("public.officialWebsite") }}</span></el-button>
          </Row>
        </Column>
      </Column>
      <Column v-if="!isEmpty(status)" class="w-100b" y="c" x="c">
        <Row class="w-100b m-b-12" x="s">
          <span class="label-sm m-b-12">{{ subStatusTimeLabel }}</span>
        </Row>
        <Row v-if="![4,5].includes(status)" class="w-100b m-b-40" x="sb">
          <Column class="bg-lGrey w-116 h-100 radius-8 p-tb-7" x="c" y="sb">
            <span class="font-42">{{ countdown.day }}</span>
            <span class="label-sm">{{ $t("form.day") }}</span>
          </Column>
          <span class="font-42 c-info">:</span>
          <Column class="bg-lGrey w-116 h-100 radius-8 p-tb-7" x="c" y="sb">
            <span class="title">{{ countdown.hours }}</span>
            <span class="label-sm">{{ $t("form.when") }}</span>
          </Column>
          <span class="font-42 c-info">:</span>
          <Column class="bg-lGrey w-116 h-100 radius-8 p-tb-7" x="c" y="sb">
            <span class="title">{{ countdown.minutes }}</span>
            <span class="label-sm">{{ $t("form.points") }}</span>
          </Column>
          <span class="font-42 c-info">:</span>
          <Column class="bg-lGrey w-116 h-100 radius-8 p-tb-7" x="c" y="sb">
            <span class="title">{{ countdown.seconds }} </span>
            <span class="label-sm">{{ $t("form.seconds") }}</span>
          </Column>
        </Row>
        <!-- <div class="h-56 f-center p-lr-20 label-sm border radius-8 bg-info c-white">{{ $t("public.finished") }}</div> -->

        <Row v-if="!isEmpty(status)" class="w-100b">
          <!-- 预热中 -->
          <Row v-if="status == 0" class="w-100b">
            <el-button v-if="[0].includes(ieoCoupons)" class="w-100b p-lr-40 label bold" type="info" disabled>{{ $t("ieo.subStatusBtn1") }}</el-button>
            <el-button v-if="[1].includes(ieoCoupons)" class="w-100b p-lr-40 title-sm" type="info" disabled>{{ $t("ieo.subStatusBtn2") }}</el-button>
          </Row>
          <!-- 进行中 -->
          <Row v-if="status == 1" class="w-100b">
            <el-button v-if="[0].includes(ieoCoupons)" class="w-100b p-lr-40 label bold" type="primary" @click="onCoupon">{{ $t("ieo.subStatusBtn1") }}</el-button>
            <el-button v-if="[1].includes(ieoCoupons)" class="w-100b p-lr-40 title-sm" type="info" disabled>{{ $t("ieo.subStatusBtn2") }}</el-button>
          </Row>
          <!-- 认购中 -->
          <Row v-if="status == 2" class="w-100b">
            <el-button v-if="subscriptionStatus == 0 && [0].includes(ieoCoupons)" class="w-100b p-lr-40 label bold" type="primary" @click="onCoupon">{{ $t("ieo.subStatusBtn1") }}</el-button>
            <el-button v-if="subscriptionStatus == 0 && [1].includes(ieoCoupons)" class="w-100b p-lr-40 label bold" type="primary" @click="onSubscribe">{{ $t("ieo.subStatusBtn3") }}</el-button>
            <el-button v-if="subscriptionStatus == 1" class="w-100b p-lr-40 label bold" type="info" disabled>{{ $t("ieo.subStatusBtn4") }}</el-button>
          </Row>
          <!-- 待发放 -->
          <Row v-if="status == 3" class="w-100b">
            <el-button v-if="subscriptionStatus == 0" class="w-100b p-lr-40 label bold" type="info" disabled>{{ $t("ieo.subStatusBtn3") }}</el-button>
            <el-button v-if="subscriptionStatus == 1" class="w-100b p-lr-40 label bold" type="primary">{{ $t("ieo.subStatusBtn4") }}</el-button>
          </Row>
          <!-- finished -->
          <Row v-if="status == 4" class="w-100b" />
        </Row>
        <Row v-if="[1,2].includes(status)" class="label-sm c-info m-t-20 w-100b " x="c">
          <span class="m-r-4">{{ $t('ieo.remainingTickets') }}:</span>
          <span class="">{{ ieoSubscriptionNum }}</span>
        </Row>
      </Column>
    </Column>
    <DialogSubscribe
      :id="id"
      v-model="showDialogSubscribe"
      :preAmountMoney="preAmountMoney"
      :ieoSubscriptionNum="ieoSubscriptionNum"
      @submit="refresh"
    />
  </Column>
</template>
<script setup>
import {
  DialogSubscribe,
  ProjectInfoHeader,
  ProjectTask,
} from '@/view/ieo/components/index'

const userStore = useUserStore()
const emit = defineEmits()
const props = defineProps({
  id: {},
  loading: {},
  status: {},
  name: {},
  fullName: {},
  synopsis: {},
  model: {},
  imgUrl: {},
  totalCopies: {},
  preAmount: {},
  preAmountMoney: {},
  currentNum: {},
  currentCopiesNum: {},
  twitterLink: {},
  telegramLink: {},
  projectWhitePaper: {},
  projectWebsite: {},
  subscriptionNum: {},
  ieoCoupons: {},
  subscriptionStatus: {},
  ieoSubscriptionNum: {},
  isLock: {},
  startTime: {},
  endTime: {},
  subStartTime: {},
  subEndTime: {},
  inputStatus: {},
  subStatus: {},
  countdown: {},
})

let showDialogSubscribe = $ref(false)

const statusLabelMap = $computed(() => ({
  0: $t('public.preheating'),
  1: $t('public.inProgress'),
  2: $t('public.subscription'),
  3: $t('public.toBeIssued'),
  4: $t('public.finished'),
  5: $t('public.unlocking'),
}))

const modelLabelMap = $computed(() => ({
  0: $t('ieo.starterMode'),
  1: $t('ieo.discountMode'),
}))

const isLockLabelMap = $computed(() => ({
  true: $t('public.true'),
  false: $t('public.false'),
}))

const statusTabClass = {
  0: 'bg-warning',
  1: 'bg-success',
  2: 'bg-primary',
  3: 'bg-error',
  4: 'bg-info',
  5: 'bg-primary',
}

const subStatusTimeLabel = $computed(() => {
  return $t(`ieo.subStatusTime${props.status + 1}`)
})
const startTime = $computed(() => dayjs(props.startTime)) // 活动开始时间戳(毫秒) 用来判断活动开始
const subStartTime = $computed(() => dayjs(props.subStartTime)) // 认购开始时间戳(毫秒) 用来判断认购开始
const subEndTime = $computed(() => dayjs(props.subEndTime)) // 认购结束时间戳(毫秒) 用来判断认购结束/分配开始
const endTime = $computed(() => dayjs(props.endTime)) // 活动结束时间戳(毫秒) 用来判断活动结束

const statusLabel = $computed(() => statusLabelMap[props.status])
const modelLabel = $computed(() => modelLabelMap[props.model])
const isLockLabel = $computed(() => isLockLabelMap[!!props.isLock])

const open = (url) => {
  if (!/^http*/.test(url)) {
    url = 'http://' + url
  }

  if (platform == 'h5') {
    window.open(url, '_blank')
  } else {
    winMes.postParent('openBrowser', { url: url })
  }
}

const refresh = () => {
  emit('refresh')
}

const onCoupon = async () => {
  const data = await ieoApi.coupon({ ieoId: props.id })
  showNotify({ type: 'success', message: $t('alert.receiveSuccess') })
  emit('refresh')
}

const onSubscribe = async () => {
  if (props.ieoSubscriptionNum == 0) {
    showNotify({ type: 'warning', message: $t('ieo.insufficientCoupons') })
    return
  }
  showDialogSubscribe = true
}

</script>

<style lang="scss">
.ProjectInfoHeader {
  .skeletonImage{
    --van-skeleton-image-size: 56px;
  }
}
</style>
