<template>
  <el-dialog
    class="dialog-d dialog-m-center dialog-header-p-0 dialog-body-p-0 dialog-footer-p-0"
    :modelValue="modelValue"
    width="90%"
    top="20vh"
    :destroy-on-close="true"
    :close-on-click-modal="false"
    :show-close="false"
    append-to-body
    @close="onClose"
  >
    <template #header>
      <Row class="font-40 p-lr-40 p-t-40 m-b-28" x="s" y="s">
        {{ $t('alert.hint') }}
      </Row>
    </template>
    <Row v-if="type ==1" class="p-lr-40 font-32 lh-48">{{ $t('ieo.activateAccelerationText') }}</Row>
    <Row v-if="type ==2" class="p-lr-40 font-32 lh-48">{{ $t('ieo.activateAccelerationText2') }}</Row>
    <template #footer>
      <Row class="w-100b p-lr-40 p-b-40 p-t-40" x="e" y="c">
        <el-button
          class="font-32 f-1 m-r-20"
          :loading="loading"
          @click="onClose"
        >{{ $t('form.cancel') }}</el-button>
        <el-button
          v-if="type ==1"
          class="font-32 f-1"
          type="primary"
          :loading="loading"
          @click="quicken"
        >{{ $t('ieo.confirmationAcceleration') }}</el-button>
        <el-button
          v-if="type ==2"
          class="font-32 f-1"
          type="primary"
          :loading="loading"
          @click="toTrade"
        >{{ $t('ieo.goPurchase') }}</el-button>
      </Row>
    </template>

  </el-dialog>
</template>
<script setup>

const emit = defineEmits(['submit'])
const props = defineProps({
  modelValue: {},
  type: {},
  ieoId: {}
})
const userStore = useUserStore()

let loading = $ref(false)

const quicken = async () => {
  try {
    loading = true
    const data = await ieoApi.quicken({ ieoId: props.ieoId, })
    if (data.status == 1) {
      ElMessage.error($t('ieo.youHaveParticipated'))
    } else {
      emit('update:modelValue', false)
      emit('submit')
    }
  } finally {
    setTimeout(() => {
      loading = false
    }, 300)
  }
}

const toTrade = () => {
  const symbol = 'BFT/USDT'
  winMes.postParent('toTrade', symbol)
}

const onClose = () => {
  emit('update:modelValue', false)
}

onBeforeMount(() => {
})
</script>

