<template>
  <Row
    class="p-30 border radius-4"
    x="sb"
    y="s"
  >
    <img class="w-66 h-66 m-r-12" :src="taskImage">
    <Row class="h-100b f-1" x="sb">
      <Column class="h-100b w-356" y="sb">
        <span class="label bold m-b-6">{{ taskTitle }}</span>
        <span class="label-sm m-b-16">{{ taskContent }}</span>
        <Row class="c-info label-sm">
          <span>
            <span class="m-r-2">{{ $t("ieo.taskEffectiveDeadline") }}:</span>
            <span>{{ dayjs(data.taskTime).format('YYYY-MM-DD HH:mm') }}</span>
          </span>
        </Row>
      </Column>
      <Row class="m-l-40" x="sb">
        <!-- 预热中 -->
        <!-- {{ status }} - {{ taskStatus }} -->
        <Row v-if="status == 0">
          <el-button
            class="h-56 border-0 c-primary p-lr-12"
            :disabled="true"
            type="primary"
            plain
          ><span class="label-sm">{{ $t("form.notStart") }}</span></el-button>
        </Row>
        <Row v-if="[1,2].includes(status)">
          <el-button
            v-if="taskStatus == 0"
            class="h-56 label-sm p-lr-12"
            type="primary"
            @click="toTask"
          ><span class="label-sm">{{ $t("form.finish") }}</span></el-button>
          <div v-if="taskStatus == 1" class="h-56 f-center p-lr-12 label-sm radius-8 bg-lGrey c-info">{{ $t("form.completed") }}</div>
        </Row>
        <!-- bg-lGrey c-info -->
        <Row v-if="[3,4].includes(status)">
          <div class="h-56 f-center p-lr-12 label-sm border radius-8 bg-info c-white">{{ $t("public.finished") }}</div>
        </Row>
      </Row>
    </Row>
  </Row>
</template>
<script setup>
import task0Image from '@/image/ieo/task-0.webp'
import task1Image from '@/image/ieo/task-1.webp'
import task2Image from '@/image/ieo/task-2.webp'
import task3Image from '@/image/ieo/task-3.webp'
import task4Image from '@/image/ieo/task-4.webp'
import task5Image from '@/image/ieo/task-5.webp'

const userStore = useUserStore()
const props = defineProps({
  data: {
    default: {},
  },
  status: {},
  taskStatus: {},
  type: {
  },
  deadline: {
  },
  symbolsAll: {},
  name: {},
})

const taskImage = $computed(() => {
  const map = {
    0: task0Image,
    1: task1Image,
    2: task2Image,
    3: task3Image,
    4: task4Image,
    5: task5Image,
  }
  return map[props.type]
})

const taskTitle = $computed(() => {
  return $t(`ieo.taskTypeMap.${props.type}`)
})

const taskContent = $computed(() => {
  const map = {
    0: () => $t.replace('ieo.taskTypeContentMap.0', { 'taskCondition': props.data.taskCondition, 'taskNum': props.data.taskNum, coinName: props.data.coinName }), // 交易提额
    1: () => $t.replace('ieo.taskTypeContentMap.1', { 'taskCondition': props.data.taskCondition, 'taskNum': props.data.taskNum, coinName: props.data.coinName, 'isAnyCoin': $t(`ieo.isAnyCoinMap.${props.data.isAnyCoin}`) }), // 充值提额
    2: () => $t.replace('ieo.taskTypeContentMap.2', { 'taskCondition': props.data.taskCondition, 'taskNum': props.data.taskNum, coinName: props.data.coinName, 'isAnyCoin': $t(`ieo.isAnyCoinMap.${props.data.isAnyCoin}`), 'isContinue': $t(`ieo.isContinueMap.${props.data.isContinue}`), 'holdDays': props.data.holdDays }), // 持币提额
    3: () => $t.replace('ieo.taskTypeContentMap.3', { 'taskCondition': props.data.taskCondition, 'taskNum': props.data.taskNum, coinName: props.data.coinName }), // 邀请提额
    4: () => $t.replace('ieo.taskTypeContentMap.4', { 'taskCondition': $t(`ieo.taskCondition5Map.${props.data.taskCondition}`), 'taskNum': props.data.taskNum, coinName: props.data.coinName }), // KYC提额
    5: () => $t.replace('ieo.taskTypeContentMap.5', { 'taskCondition': props.data.taskCondition, 'taskNum': props.data.taskNum, coinName: props.data.coinName }), // 交易费率等级达标领券
  }
  return map[props.type]()
})
// 交易提额
// 任务期间累计交易100 USDT可获得1张认购券。
// Trade 100 USDT during the mission time and get 1 ticket

// 充值提额
// 任务期间累计充值100 USDT 可以获得1张认购券。
// Top up $100 during the mission time and you will receive 1 ticket.

// 持币提额
// 任务期间连续持有100 USDT累计3天，即可获得1张认购券
// By holding 200 USDT for 3 consecutive days during the mission, you will receive 3 tickets.

// 邀请提额
// 任务期间累计邀请2名新用户，可获2张认购券
// If you invite 2 users during the event, you will receive 2 tickets.

// KYC提额
// 完成高级实名认证可以获得1张认购券
// Complete a high level of KYC and you can get 1 ticket.

const task0 = () => {
  if (platform.toLowerCase() == 'ios') {
    winMes.postParent('toTrade')
  } else {
    let coinName = props.name.toUpperCase()
    const hasCoin = props.symbolsAll.find(e => e.coinMarket.split('/')[0] == coinName)
    if (isEmpty(hasCoin)) {
      coinName = 'BTC'
    }
    if (coinName == 'USDT') {
      coinName = 'BTC'
    }
    const coinMarket = `${coinName}/USDT`
    console.log('coinMarket', coinMarket)
    winMes.postParent('toTrade', coinMarket)
  }
}

const task1 = () => {
  winMes.postParent('toRecharge')
}

const task2 = () => {
  winMes.postParent('toHold')
}

const task3 = () => {
  winMes.postParent('toInvited')
}

const task4 = () => {
  winMes.postParent('toKYC')
}

const task5 = () => {
  winMes.postParent('toTrade')
}

const toTask = () => {
  const tasks = {
    0: task0,
    1: task1,
    2: task2,
    3: task3,
    4: task4,
    5: task5,
  }
  if (isEmpty(userStore.token)) {
    router.push('/login')
  } else {
    tasks[[props.type]]()
  }
}
</script>
